import React from "react"
import view from "./Creators.module.css"

function MainCreators(props) {

    let newCreatorElement = React.createRef();

    console.log(props.creatorUser.creatorName)

    let addCreator = () => {
        if (newCreatorElement.current !== null) {
            props.onAddCreator();
        }
    }

    let updateCreator = () => {
        let text = newCreatorElement.current.value;
        props.onUpdateCreator(text);
    }

    return (
        <div>
            <div className={view.main}>
                <div>
                    {props.creatorUser.creatorData.map(c =>
                        <div>
                            <textarea ref={newCreatorElement} onChange={updateCreator}
                                      value={c.name}>
                            </textarea>
                            <br/>

                            <button onClick={addCreator}>Add creator</button>
                        </div>
                    )
                    }
                </div>
            </div>
        </div>
    )
}

export default MainCreators;
