import {connect} from "react-redux";
import {addCreatorActionCreator, updateCreatorActionCreator} from "../../redux/creator_reducer";
import MainCreators from "./Creators";

let mapStateToProps = (state) => {
    return {
        creatorUser: state.creatorPage
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        onAddCreator: () => {
            dispatch(addCreatorActionCreator())
        },
        onUpdateCreator: (text) => {
            dispatch(updateCreatorActionCreator(text))
        }
    }
}

const CreatorsContainer = connect(mapStateToProps, mapDispatchToProps)(MainCreators);


export default CreatorsContainer;

